@import "../../styles/main.module.scss";

header {
  height: 8vh;
  background-color: $secondary;
  color: $primary;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 1px 1px 1px rgb(114, 86, 86);


  


  .navBar {
    display: flex;
      padding: $sp-m $mn;
      @include navText;
  
    .navList {
        width: 90%;
        display: flex;
        justify-content: space-evenly;
        list-style: none;
        padding: 0 $mn;
    }
  
    .cart {
        width: 10%;
    }
  
  }
  

}


